import React from 'react';
import Typography from '@material-ui/core/Typography';

import '../App.css';

export default function FAQ() {
  return(
    <div className="wrapperqanda">
    <Typography
        component="h1"
        variant="h3"
        align="center"
        gutterBottom
        className="qanda"
      >Questions et Réponses</Typography>
    <Typography
        component="h4"
        variant="h5"
        align="center"
        gutterBottom
        className="question"
      >Est-ce confirmé que le mariage sera cette année?</Typography>
    <div>Nous aimerions bien vous dire oui à 100%, mais malheureusement, les deux dernières années nous ont prouvé que rien ne peut être certain. Nous voulons que notre mariage soit exactement comme nous l'imaginons et donc sans masque, entourés de toutes les personnes qui nous sont chères et d'être en mesure de danser toute la soirée! Si ces conditions ne sont pas possibles, nous reporterons encore une fois d'un an. Par contre, nous sommes très confiants que cette année sera la bonne. </div>
    <br />
    <Typography
        component="h4"
        variant="h5"
        align="center"
        gutterBottom
        className="question"
      >Où se déroulera le mariage?</Typography>
    <div>Nous sommes chanceux de pouvoir célébrer notre mariage au chalet familial de Mélyssa à Sutton. Selon la température, la cérémonie devrait se dérouler à l'extérieur et la réception sera sous un chapiteau. Vous n'aurez pas à changer de lieu, car ils seront à quelques mètres l'une de l'autre. </div>
    <br />
    <Typography
        component="h4"
        variant="h5"
        align="center"
        gutterBottom
        className="question"
      >J'ai des difficultés à me déplacer, y aura-t-il beaucoup de marche?</Typography>
    <div>Il y aura quelques minutes de marche à faire sur un terrain dénivelé. Il y aura donc des VTTs afin d'aider ceux dans le besoin. </div>
    <br />
    <Typography
        component="h4"
        variant="h5"
        align="center"
        gutterBottom
        className="question"
      >Y a-t-il un code vestimentaire?</Typography>
    <div>Tenue de soirée d'été - habits, pantalons et chemise, robes courtes, robes longues, ... Soyez vous mêmes et confortables! </div>
    <div>IMPORTANT - Pas de talons hauts!! La soirée se déroulera dans le gazon et nous voulons que vous soyez confortables pour en profiter le plus possible.</div>
    <br />
    <Typography
        component="h4"
        variant="h5"
        align="center"
        gutterBottom
        className="question"
      >Avez-vous un registre de cadeaux?</Typography>
    <div>Nous sommes très chanceux d'avoir une maison remplies d'amour, de rire et complètement meublées. Pour nous, le plus important est de vous avoir parmi nous afin de célébrer cette journée spéciale. Par contre, si vous souhaitez nous offrir un cadeau, nous accepterons gracieusement une contribution monétaire pour notre lune de miel. Il y aura une boite à cet effet le jour du mariage. </div>
    <br />
    <Typography
        component="h4"
        variant="h5"
        align="center"
        gutterBottom
        className="question"
      >J'aimerais dormir proche, y aura-t-il des hébergements disponibles?</Typography>
    <div>Plus de détails suivront à ce sujet. Nous travaillons à avoir une offre d'hotel pour vous et possiblement un service de navette. </div>
    <br />
    <Typography
        component="h4"
        variant="h5"
        align="center"
        gutterBottom
        className="question"
      >Quand est-ce que je dois confirmer ma présence (RSVP)?</Typography>
    <div>Nous enverrons les invitations officielle au printemps. Nous vous communiquerons la date limite exacte à ce moment. Il sera possible de répondre à l'invitation directement sur le site web. </div>
    <br />
    <Typography
        component="h4"
        variant="h5"
        align="center"
        gutterBottom
        className="question"
      >Puis-je inviter quelqu'un d'autre?</Typography>
    <div>Les invitations seront adressées directement aux personnes invitées au mariage. Nous ne pouvons pas permettre à tout le monde d'apporter un accompagnateur. Si vous voulez inviter quelqu'un d'autre, nous vous demandons de communiquer avec nous le plus tôt possible. </div>
    
    
    </div>
  );
}