import React from 'react';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper} from '@material-ui/core';

import ScheduleOTD from '../images/schedulefr.jpg';

import '../App.css'

function createData(name, calories) {
  return { name, calories};
}

const rows = [
  createData('3-4pm', 'GUESTS ARRIVAL'),
  createData('', 'We invite all of our guests to get their pictures taken by our photographers upon arrival. This will ensure that we have nice pictures of everyone attending our wedding. '),
  createData('4pm', 'CEREMONY'),
  createData('', 'There will be seats available for people who need them, but it is mostly a standing ceremony. This will be an unplugged ceremony i.e no pictures or videos as we will have our own team to take care of that. '),
  createData('4:30-6pm', 'COCKTAIL HOUR'),
  createData('', "There will be some finger food available during cocktail hour, but don't spoil your appetite!"),
  createData('6pm-3am', 'RECEPTION'),
  createData('', 'Mechoui seated dinner. This is an all-included event. Expect an open bar with wine, beer and liquor, as well as alcohol free options. We want it to be a worry free night for all to enjoy. Please note that all of our staff has already been tipped.')

];

export default function Schedule() {
  return(

    <div className="wrappersched">
    <h1>Horaire de la journée</h1>
    <img style={{maxWidth: "100%"}} src={ScheduleOTD} alt="Schedule of the day"></img>

    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} aria-label="simple table">
        <TableBody>
          {rows.map((row) => (
            <TableRow
              key={row.name}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell component="th" scope="row">
                {row.name ? (<strong>{row.name}</strong>) : row.name}
              </TableCell>
              <TableCell style={{ maxWidth: '300px'}}align="left">{row.name ? (<strong>{row.calories}</strong>) : row.calories}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
    </div>
  );
}