import React from 'react';

import Grid from '@material-ui/core/Grid';

import Typography from '@material-ui/core/Typography';

import Leaf from '../images/hesonehp.png';
import Gallery1 from '../images/gallery1.jpg';
import Gallery2 from '../images/gallery2.jpg';
import Gallery3 from '../images/gallery3.jpg';
import '../App.css';

export default function Home() {

  return(
    <span>
        <div className="names">
              <img
                className="topLeaf"
                style={{ width: '415px', maxWidth: '100%',}}
                src={Leaf}
                alt="Leaf"
              />
        <Typography className="fullNames" component="h3" variant="h2">
          Mélyssa Mckay-Nicole
          <br />
          <span className="smallAnd">and</span>
          <br />
          Michael Doseger
        </Typography>
      <img style={{ width: '415px', maxWidth: '100%', }} src={Leaf} alt="Leaf" />
      </div>
      <div className="story">
        <Grid container spacing={0}>
          <Grid className="leftTitle" item xs={12} md={6}>
            <Typography component="h4" variant="h4">
              NOTRE HISTOIRE
            </Typography>
            <span className="storyDate">14 août 2010</span>
          </Grid>
          <Grid item xs={12} md={1}></Grid>
          <Grid item xs={12} md={4}>
            <span style={{ fontSize: '1.75em', maxWidth: '95%', display: 'inline-block' }}>Cette histoire d'amour a commencé avec deux jeunes de 17 ans qui pensaient tout savoir. Mais ce qu’ils ne savaient pas, c’est qu’ils s’apprêtaient ensemble à traverser les hauts et les bas de la vie et qu’ils se soutiendraient à travers toutes les épreuves. Ils sont passés d'adolescents sortant du secondaire à des adultes qui construisent maintenant leur carrière ainsi que leur vie entière ensemble. Bien qu'ils le sachent depuis longtemps maintenant, ils sont maintenant prêts à s'engager l'un envers l'autre pour toujours et à jamais.
</span>
          </Grid>
          <Grid item xs={12} md={1}></Grid>
          <Grid className="gridSpacer" item xs={12}></Grid>
          <Grid className="leftTitle" item xs={12} md={6}>
            <Typography component="h4" variant="h4">
              LA GRANDE DEMANDE
            </Typography>
            <span className="storyDate">14 août 2020</span>
          </Grid>
          <Grid item xs={12} md={1}></Grid>
          <Grid item xs={12} md={4}>
            <span style={{ fontSize: '1.75em', maxWidth: '95%', display: 'inline-block' }}>Traversant une pandémie et ayant du annulé leur voyage en Espagne où Michael avait initialement prévu de demander à Melyssa, il a dû faire preuve d’imagination pour trouver une nouvelle idée. Proposer le jour de leur 10e anniversaire semblait être la meilleure option. Allant prendre des «photos» sur les bord du lac à Lachine, Michael a complètement pris Mélyssa par surprise, même si elle avait reçu de petits indices tout au long de la semaine, ce qui en a fait une parfaite grande demande. Ils espèrent maintenant que vous les rejoindrez pour la célébration de leur amour à l'occasion de leur 12e anniversaire le 13 août 2022.
</span>
          </Grid>
          <Grid item xs={12} md={1}></Grid>
          <Grid className="gridSpacer" item xs={12}></Grid>
        </Grid>
        <Grid container spacing={0}>
          <Grid className="gridSpacer" item xs={12}></Grid>
          <Grid item xs>
            <img
              className="galleryImagesLg"
              src={Gallery1}
              alt="Mike and Mel"
            />
            <img
              className="galleryImagesLg"
              src={Gallery2}
              alt="Mike and Mel"
            />
            <img
              className="galleryImagesLg"
              src={Gallery3}
              alt="Mike and Mel"
            />
          </Grid>
          <Grid className="gridSpacer" item xs={12}></Grid>
        </Grid>
      </div>
    </span>
  );
}