import React from 'react';

export default function RSVP(props) {
  const {
    iframeSource = '<iframe src="https://docs.google.com/forms/d/e/1FAIpQLSdPc2FvBUgEK1alLDlzoD8ittcgOsaUwr62hvOQp39KpwVghQ/viewform?embedded=true" width="640" height="771" frameborder="0" marginheight="0" marginwidth="0">Loading…</iframe>'
  } = props;
    return(
    <div style={{margin: "50px 20%"}}>
      <h1>RSVP</h1>
      <div dangerouslySetInnerHTML={{__html: iframeSource}}></div>
      <br />

    </div>
  );
}